<style lang="scss" scoped>
.style__box {
  height: 440px;
  overflow: hidden;
  margin-top: 120px;
}
.blue-outer {
  width: 2000px;
  height: 1000px;
  top: 0;
  margin-left: calc(50% - 2000px / 2);
  background-color: $Primary;
  border-radius: 50%;
}
.circle__orange {
  background-color: $Secondary;
  border-radius: 50%;
  position: absolute;
  &1 {
    width: 400px;
    height: 400px;
    top: 200px;
    left: 210px;
  }
  &2 {
    width: 200px;
    height: 200px;
    top: 140px;
    right: 340px;
  }
}
.copyRight {
  width: 300px;
  font-size: $Text_XSmall;
  color: rgba(255, 255, 255, 0.75);
  position: absolute;
  top: 330px;
  left: 50%;
  transform: translateX(-50%);
}
.button {
  position: absolute;
  cursor: pointer;
  &__up {
    width: 36px;
    right: 492px;
    color: $Off_White;
    @media (max-width: 1050px) {
      right: 692px;
    }
    @media (max-width: 672px) {
      right: 752px;
    }
    @media (max-width: 576px) {
      right: 862px;
    }
    &__icon {
      top: 120px;
      font-size: 36px;
      @media (max-width: 672px) {
        top: 70px;
      }
    }
    &__text {
      top: 176px;
      font-size: $Text_Small;
      @media (max-width: 672px) {
        top: 126px;
      }
    }
  }
}
.footer {
  &__container {
    width: 1026px;
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__info {
    margin-left: 120px;
    .socialMedia-icon {
      margin-right: 1em;
    }
    &__contactInfo > p {
      text-align: left;
      margin-bottom: 4px;
      color: $Off_White;
    }
    &__nav {
      @media (max-width: 992px) {
        display: none;
      }
      .nav__list {
        list-style-type: none;
        padding-left: 0;
        margin-bottom: 0;
      }
      .nav__item {
        padding: 0 2em;
        font-size: $Text_Medium;
        > a {
          color: $Off_White;
          text-decoration: none;
        }
      }
    }
  }
}
</style>

<script>
export default {
  name: "Footer",
  data() {
    return {
      phoneNumber: "02xx-xxx-xxx",
      address: "台北市中山區明水路636號",
    };
  },
  methods: {
    scrollTop() {
      setTimeout(() => {
        window.scrollTo(0, 0);
      }, 100);
    },
  },
};
</script>
